import Vue from 'vue'
export default {
	data() {
		return {
			roomId: '',
			tel: '',
			isArrival: true,
			status: null,
			time1: null,
			time2: null,
			time3: null,
			timeSelect: 0,
			timeResult: null,
			id: null
		};
	},
	onLoad(options) {
		// this.bookStatus = options.status
		this.id = options.id
		// console.log(options);
		var time = new Date()
		var hour = time.getHours()
		var min = time.getMinutes()
		var min1 = min + 20 > 60 ? min + 20 - 60 : min + 20
		min1 = min1 < 10 ? '0' + min1 : min1
		var hourNew = hour + 1
		var min2 = min + 40 > 60 ? min + 40 - 60 : min + 40
		min2 = min2 < 10 ? '0' + min2 : min2
		
		this.time1 = min + 20 > 60 ? hourNew + ':' + min1 : hour + ':' + min1
		this.time2 = min + 40 > 60 ? hourNew + ':' + min2 : hour + ':' + min2,
		min = min < 10 ? '0' + min : min
		this.time3 = hourNew + ':' + min
		
		var date = new Date()
		var year = date.getFullYear()
		var m = date.getMonth()
		m += 1
		var month = m < 10 ? '0' + m : m
		var d = date.getDate()
		var day = date.getDate() < 10 ? '0' + d : d
		var newDate = year + "-" + month + "-" + day + " " + this.time1 + ':00'
		this.timeResult = newDate
	},
	methods:{
		GetUserInfo(){
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'user/info',
				success(res) {
					if(res.data.code == 0){
						Vue.prototype.userServiceInfo = res.data.data
						uni.showToast({
							title: '预约成功',
							duration:1500
						})
						setTimeout(() => {
							uni.redirectTo({
								url: '../booking/booking'
							})
						}, 1500)
						
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
				}
			})
		},
		checkTel(){
			if(this.tel != '' && this.tel != '' && this.tel.length != 11){
				uni.showModal({
					content: '请填写正确手机号'
				})
			}
		},
		goBook(){
			var that = this
			if(this.isArrival == true){
				if(this.roomId == ''){
					uni.showModal({
						content: '请填写房间号'
					})
				} else {
					uni.request({
						method: "POST",
						header: {
							token: that.Token
						},
						url: that.Server + 'order/add',
						data: {
							isArrival: that.isArrival,
							roomId: that.roomId,
							staffId: parseInt(that.id),
							// tel: "",
							// arriveTime: ''
						},
						success(res) {
							if(res.data.code == 0){
								that.GetUserInfo()
							} else if (res.data.code == 101) {
								uni.showToast({
									title: '登录超时，将重新授权登录',
									duration: 500
								})
								setTimeout(() => {
									uni.reLaunch({
										url: '../login/login'
									})
								}, 500)
							} else {
								uni.showModal({
									content: res.data.msg
								})
							}
						}
					})
				}
			} else {
				if(this.tel == '' || this.tel.length != 11){
					uni.showModal({
						content: '请填写正确手机号'
					})
				} else {
					uni.request({
						method: "POST",
						header: {
							token: that.Token
						},
						url: that.Server + 'order/add',
						data: {
							isArrival: that.isArrival,
							// roomId: that.roomId,
							staffId: parseInt(that.id),
							tel: that.tel,
							arriveTime: that.timeResult
						},
						success(res) {
							if(res.data.code == 0){
								that.GetUserInfo()
							} else if (res.data.code == 101) {
								uni.showToast({
									title: '登录超时，将重新授权登录',
									duration: 500
								})
								setTimeout(() => {
									uni.reLaunch({
										url: '../login/login'
									})
								}, 500)
							} else {
								uni.showModal({
									content: res.data.msg
								})
							}
						}
					})
				}
			}
			
		},
		selectTime(e){				
			this.timeSelect = e.currentTarget.dataset.target
			var date = new Date()
			var year = date.getFullYear()
			var m = date.getMonth()
			m += 1
			var month = m < 10 ? '0' + m : m
			var d = date.getDate()
			var day = date.getDate() < 10 ? '0' + d : d
			var newDate = year + "-" + month + "-" + day + " " + e.currentTarget.dataset.time + ':00'
			this.timeResult = newDate
		},
		changeBookStatus(){
			if(this.isArrival != true){
				this.isArrival = true
			}
		},
		changeBookStatusB(){
			if(this.isArrival != false){
				this.isArrival = false
			}
			
		},
	}
}
